            <text x="0.75" y="15.75" font-size="2.8" class="coordinate-dark">7</text>
            <text x="0.75" y="28.25" font-size="2.8" class="coordinate-light">6</text>
            <text x="0.75" y="40.75" font-size="2.8" class="coordinate-dark">5</text>
